[class$=__contenedor] {
  max-width: 1000px;
  width: 90%;
  margin: 0 auto;
}

.text-center {
  text-align: center;
}

.vertical-center {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.semi-bold {
  font-weight: 500;
}

.bold {
  font-weight: 700;
}

/** Media Queries **/
html {
  box-sizing: border-box;
  font-size: 14px;
}

*, *:after, *:before {
  box-sizing: inherit;
}

@media (min-width: 400px) {
  html {
    font-size: 16px;
  }
}
@media (min-width: 1024px) {
  html {
    font-size: 18px;
  }
}
body {
  font-size: 1rem;
  font-family: "Spline Sans Mono", monospace;
  line-height: 1.5;
  font-size: clamp(1rem, 0.34vw + 0.91rem, 1.19rem);
}

img,
video,
audio {
  max-width: 100%;
  height: auto;
  display: block;
}

a {
  text-decoration: none;
  color: #000;
}

h1, h2, h3 {
  margin: 0 0 3.125rem 0;
  line-height: 1.2;
  font-size: clamp(1.56rem, 1vw + 1.31rem, 2.11rem);
}

h1 {
  font-weight: 900;
}

h2 {
  font-weight: 700;
}

h3 {
  font-weight: 700;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

blockquote {
  margin: 0;
}

.header {
  background-color: #004cd1;
}
.header__contenedor {
  padding-block: 1.25rem;
}
@media (min-width: 768px) {
  .header__contenedor {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
.header__h1 {
  margin-block-end: 1.25rem;
  color: #FFFFFF;
}
@media (min-width: 768px) {
  .header__h1 {
    margin: 0;
  }
}

.nav {
  display: flex;
  justify-content: space-evenly;
}
@media (min-width: 768px) {
  .nav {
    gap: 1.25rem;
  }
}
.nav__item {
  display: flex;
}
.nav__item--actual {
  border-block-end: 2px solid #FFFFFF;
}
.nav__icono {
  color: #FFFFFF;
}
.nav__link {
  color: #FFFFFF;
}

.calculadora {
  margin-block-start: 2.5rem;
  padding-block-start: 1.25rem;
  box-shadow: 1px 1px 10px 1px rgba(0, 0, 0, 0.35);
}
@media (min-width: 1024px) {
  .calculadora {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
  }
}
.calculadora__header {
  font-size: clamp(1rem, 0.34vw + 0.91rem, 1.19rem);
  margin-block-end: 1.25rem;
}
.calculadora__input {
  inline-size: 3.125rem;
  border-radius: 5px;
}
.calculadora__input--error {
  border-color: red;
}
.calculadora__btn {
  background-color: #004cd1;
  color: #FFFFFF;
  font-weight: 700;
  border: none;
  padding: 0.3125rem;
  border-radius: 5px;
}
.calculadora__btn:hover {
  background-color: #055fff;
}
.calculadora:last-child {
  margin-block-end: 2.5rem;
}
.calculadora__error {
  border: 1px solid red;
  inline-size: 80%;
  margin-inline: auto;
  border-radius: 5px;
  color: red;
}

@media (min-width: 1024px) {
  .main__contenedor {
    display: grid;
    gap: 1.25rem;
    grid-template-columns: repeat(2, 1fr);
  }
}

.resultado__contenido {
  padding-block: 1.25rem;
}

.footer {
  background-color: #000;
  color: #FFFFFF;
  font-size: 13px;
}
.footer__contenedor {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2.5rem;
  padding-block: 1.25rem;
}
@media (min-width: 768px) {
  .footer__contenedor {
    display: grid;
    gap: 2.5rem;
    grid-template-columns: repeat(2, 1fr);
  }
}

.pwa {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.625rem;
}
.pwa__info {
  margin: 0;
}
.pwa__guia {
  color: #FFFFFF;
}

.contacto {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.625rem;
}
.contacto__desarrollador, .contacto__cps {
  margin: 0;
}
.contacto__redes {
  display: flex;
  flex-direction: column;
  gap: 0.3125rem;
  align-items: center;
}
.contacto__red {
  color: #FFFFFF;
  background-color: #1a1a1a;
  padding: 0.1875rem;
  border-radius: 5px;
}
.contacto__red:first-child {
  display: flex;
  align-items: center;
  gap: 0.3125rem;
}
.contacto__red:first-child::after {
  content: "";
  background-image: url(../img/instagram.png);
  background-repeat: no-repeat;
  width: 15px;
  height: 15px;
  background-size: cover;
}
.contacto__red:last-child {
  display: flex;
  align-items: center;
  gap: 0.3125rem;
}
.contacto__red:last-child::after {
  content: "";
  background-image: url(../img/twitter.png);
  background-repeat: no-repeat;
  width: 15px;
  height: 15px;
  background-size: cover;
}

.creditos {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-block-end: 1.25rem;
}
.creditos__credito {
  color: #FFFFFF;
}
.creditos__pixabay {
  margin: 0;
}